$mainColor : #ffb43a;
$hoverColor : hsl(37 , 94% , 57%);
$bodyColor : #1e1e2a;
$containerColor : #2d2e37;
$textColor : #fcfeff;
@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@500;700;900&display=swap');
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  scroll-behavior:smooth ;
  font-family: 'Poppins', sans-serif;
}
body , html {
  background-color: $bodyColor;
}
#header{
  width: 85%;
  margin: 0 auto;
  height: 70px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 30px;
  .search {
    display: flex;
      align-items: center;
     img{
      width: 40px;
     }
     div{
      display: flex;
      align-items: center;
      padding: 0 10px;
      margin: 0 7px;
      border-radius: 20px;
      background-color: $containerColor;
      button{
        outline: none;
        border: none;
        background-color: transparent;
        font-size: 1.4rem;
        margin-top: 8px;
        cursor: pointer;
        transition: 0.5s;
        &:hover{
          color: $hoverColor;
        }
       }
       input{
        outline: none;
        border: none;
        background-color: transparent;
        color: $textColor;
        &::placeholder{
          color: rgba(0, 0, 0, 0.479);
        }
       }
     }
   
  }
}
.webLogo{
    font-family: 'Roboto', sans-serif;
    font-weight: 900;
    font-size: 1.8rem;
    color: $textColor;
    span{
      color: $mainColor;
    }
}
#sidebar{
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100px;
  z-index: 2;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
  gap: 30px;
  
  a{
    display: flex;
    flex-direction: column;
    align-items: center;
    text-decoration: none;
    color: $textColor;
    span{
      font-size: 0.7rem;
      transition: 0.5s;
      letter-spacing: 2px;
    }
    .icon{
      font-size: 1.6rem;
      margin-bottom: -10px;
    }
    &:hover span{
      color: $hoverColor;
    }
  }
}
.carousel-box{
  .content{
    box-shadow: 0px 0px 50px;
    transform: translate(10%,50%);
    width: 450px;
    padding: 20px;
    background-color: rgba(0, 0, 0, 0.575);
    border-radius: 10px;
    .carousel-logo{
      width: 250px;
    }
  }
  width: 80%;
  overflow-x: hidden;
  height: 450px;
  margin: 3rem auto;
  border-radius: 10px;
  p{
    width: 100%;
    font-size: 14px;
    color: $textColor
  }
}

.btn{
  cursor: pointer;
  border: none;
  outline: none;
  background-color: transparent;
  border:2px solid  $mainColor;
  color: $mainColor;
  padding:10px 20px;
  transition: 0.5s;
  margin: 10px 0;
  &:hover{
    background-color: $mainColor;
    color: $textColor;
  }
}



#carousel-box1{
  background: url(../assets/movies/bg1.jpeg);
  background-position: center;
  background-size: cover;
  .carousel-logo{
    width: 320px;
  }
}
#carousel-box2{
  background: url(../assets/movies/bg2.jpeg);
  background-position: center;
  background-size: cover;
  .carousel-logo{
    width: 160px;
  }
}
#carousel-box3{
  background: url(../assets/movies/bg3.jpeg);
  background-position: center;
  background-size: cover;
  .carousel-logo{
    width: 200px;
  }
}
#carousel-box4{
  background: url(../assets/movies/bg4.jpg);
  background-position: center;
  background-size: cover;
}
#carousel-box5{
  background: url(../assets/movies/bg5.jpeg);
  background-position: center;
  background-size: cover;
}
.row{
  width: 80%;
  margin: 0 auto;
  p{
    background-color: $containerColor;
    color: white;
    text-align: center;
    padding: 13px 0;
    letter-spacing: 1px;
    border-radius: 5px;
    font-size: 1.1rem;
    color: $mainColor;
  }

  .img-row{
    display: flex;
    margin: 2rem 0;
    gap: 20px;
    height: 230px;
    overflow-x: scroll;
    &::-webkit-scrollbar{
      display: none;
    }
    img{
      width: 150px;
      border-radius: 10px;
    }
  }
}
.btn-p{
  position: absolute;
  left: 45%;
}

#footer{
  width: 85%;
  transform: translateX(10%);
  gap: 20px;
  display: flex;
  margin-top: 8rem;
  margin-bottom: 3rem;
  .part1 , .part2 {
    width: 50%;
    display: flex;
    gap: 20px;
  }
  .section{
    width: 50%;
    display: flex;
    flex-direction: column;
    .social-icons{
      font-size: 1.8rem;
      width: 270px;
      display: flex;
      justify-content: space-between;
      cursor: pointer;
      color: $mainColor;
    }
    .form{
      width: 270px;
      margin: 1rem 0;
      height: 43px;
      background-color: $containerColor;
      display: flex;
      justify-content: space-between;
      input , button{
        border: none;
        outline: none;
        height: 100%;
      }
      button{
        padding: 5px;
        cursor: pointer;
        background-color: $mainColor;
        font-weight: 500;
        color: $textColor;
        font-size: 15px;
      }
      input{
        background-color: transparent;
      }
    }
  }
  .section2{
    justify-content: center;
    
    align-items: center;
    a{
      color: $textColor;
      transition: 0.5s;
      text-decoration: none;
      font-size: 18px;
      &:hover{
        color: $hoverColor;
      }
    }

  }
  .section3{
    p{
      font-size: 15px;
      color: $textColor;
    }
  }
  .section4{
    p{
      color: $mainColor;
      font-size: 13px;
    }
  }
}
.footer-headings{
  font-family: 'Roboto', sans-serif;
    font-weight: 900;
    font-size: 1.5rem;
    color: $mainColor;
}
.last{
  font-size: 18px;
  margin-bottom: 1rem;
  text-align: center;
  color: $mainColor;
  span{
    color: $textColor;
  }
}


//Responsiveness

@media screen and (max-width:1000px) {
  #footer{
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 50px;
    .part1,.part2{
      width: 90%;
    }
  }

  #carousel-box1{
    .carousel-logo{
      width: 170px;
    }
  }
  #carousel-box2{
    .carousel-logo{
      width: 110px;
    }
  }
  #carousel-box3{
    .carousel-logo{
      width: 150px;
    }
  }
  #carousel-box4{
    .carousel-logo{
      width: 200px;
    }
  }
  #carousel-box5{
    .carousel-logo{
      width: 200px;
    }
  }
  .content{
    width: 350px;
  }
  .carousel-box{
    height: 400px;
  }
}

@media screen and (max-width:800px){
  .row{
    width: 97%;
  }
  .carousel-box{
    width: 97%;
  }
  #sidebar{
    height: 80px;
    background-color: $containerColor;
    bottom: 0;
    left: 0;
    width: 100%;
    flex-direction: row;
    gap: 30px;
  }
  .last{
    margin-bottom: 100px;
  }
}
@media screen and (max-width:550px){
  .carousel-box{
    display: flex;
    justify-content: center;
    align-items: center;
    height: 350px;
    .content{
      transform: translate(0%,0%);
    }
  }

  .carousel-box{
    p{
      font-size: 10px;
    }
  }
  #carousel-box1{
    .carousel-logo{
      width: 120px;
    }
  }
  #carousel-box2{
    .carousel-logo{
      width: 60px;
    }
  }
  #carousel-box3{
    .carousel-logo{
      width: 100px;
    }
  }
  #carousel-box4{
    .carousel-logo{
      width: 150px;
    }
  }
  #carousel-box5{
    .carousel-logo{
      width: 150px;
    }
  }
  #sidebar{
    a{
      span{
        font-size: 0.5rem;
        display: none;
      }
      .icon{
        display: block;
        font-size: 2rem;
      }
    }
  }
  #footer{
    .part1,.part2{
      flex-direction: column;
      justify-content: center;
      align-items: center;
    }
    .section2{
      align-items: flex-start;
    }
  }
}

@media screen and (max-width:490px){
  .searchbar{
    display: none !important;
  }
  #footer{
    .section{
      width: 100%;
    }
  }
  .last{
    font-size: 15px;
  }
  .content{
    width: 300px !important;
  }
  .carousel-box{
    height: 280px;
  }
}

@media screen and (max-width:350px){
  #sidebar{
    a{
      .icon{
        font-size: 1.6rem;
      }
    }
  }
}